@import "../scss/variables";

/* ###### Sidemenu ###### */

.app-sidebar {
	.mCSB_container {
	  margin-right: 0px;
	}
  
	.mCSB_draggerContainer {
	  right: -11px;
	}
  }
  
  @media (min-width: 768px) {
	.app.sidenav-toggled {
	  &.sidenav-toggled1 .app-content {
		margin-left: 0;
	  }
  
	  .app-sidebar__overlay {
		visibility: hidden;
	  }
	}
  }
  
  @media (max-width: 767px) {
	.app {
	  overflow-x: hidden;
  
	  .app-sidebar {
		left: -330px;
	  }
  
	  .app-sidebar__overlay {
		visibility: hidden;
	  }
  
	  &.sidenav-toggled {
		.app-content {
		  margin-left: 0;
		}
  
		.app-sidebar {
		  top: 0;
		  padding-top: 52px;
		}
	  }
  
	  &.sidebar-mini.sidebar-gone {
		.second-sidemenu, &.sidenav-toggled.sidenav-mobile .second-sidemenu {
		  visibility: hidden;
		}
  
		&.sidenav-mobile.sidenav-toggled4.sidenav-toggled1.sidenav-toggled .second-sidemenu {
		  visibility: visible;
		}
  
		&.sidenav-toggled4 .app-content {
		  margin-left: 0;
		}
	  }
	}
  
	.nav-link.nav-link.toggle {
	  -webkit-box-ordinal-group: 0;
	  -ms-flex-order: -1;
	  order: -1;
	}
  
	.search-show .app-sidebar {
	  z-index: 1;
	}
  
	.header.app-header .form-control.header-search {
	  margin-top: 0px !important;
	}
  
	.app.sidenav-toggled1 .app-sidebar {
	  left: 0;
	}
  
	.app-content {
	  margin-top: 35px;
	}
  }
  
  .app-content {
	min-height: calc(100vh - 50px);
	margin-top: 45px;
	margin-bottom: 0 !important;
	-webkit-transition: margin-left 0.3s ease;
	-o-transition: margin-left 0.3s ease;
	transition: margin-left 0.3s ease;
	padding: 18px 1.2rem 0 1.2rem;
	overflow: hidden;
  
	.side-app {
	  padding: 40px 30px 0 30px;
	}
  }
  
  .footer .container {
	width: 100%;
	padding-right: 0.75rem;
	padding-left: 0.75rem;
	margin-right: auto;
	margin-left: auto;
  }
  
  @media (min-width: 768px) {
	.app-content {
	  margin-left: 343px;
	}
  }
  
  @media (max-width: 767px) and (min-width: 568px) {
	.app-content {
	  min-width: 100%;
	  margin-top: 35px;
	}
  
	.header-secondary .col:first-child {
	  margin-bottom: 5px !important;
	}
  }
  
  @media (max-width: 575.98px) and (min-width: 361px) {
	.second-sidemenu {
	  width: 230px !important;
	}
  }
  
  @media (max-width: 360px) {
	.app-content {
	  margin-top: -12px;
	  min-width: 100%;
	}
  
	.second-sidemenu {
	  width: 200px !important;
	}
  }
  
  @media (max-width: 480px) {}
  
  @media print {
	.app-content {
	  margin: 0;
	  padding: 0;
	  background-color: $white;
	}
  }
  
  /*----- Componant: Top Navigation Bar ----- */
  
  .app-header {
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	width: 100%;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	z-index: 99;
	padding-right: 10px;
	padding: 10px 0px;
	box-shadow: 0 0 0 0px rgba(61, 119, 180, 0.12), 0 8px 16px 0 rgba(91, 139, 199, 0.24);
	border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  }
  
  @media (min-width: 768px) {
	.app-header {
	   padding-right:25px !important;
	}
  }
  
  @media print {
	.app-header {
	  display: none;
	}
  }
  
  .app-header__logo {
	-webkit-box-flex: 1;
	-ms-flex: 1 0 auto;
	flex: 1 0 auto;
	color: $white;
	text-align: center;
	font-family: 'Niconne';
	padding: 0 15px;
	font-size: 26px;
	font-weight: 400;
	line-height: 50px;
  
	&:focus, &:hover {
	  text-decoration: none;
	}
  }
  
  @media (min-width: 768px) {
	.app-header__logo {
	  -webkit-box-flex: 0;
	  -ms-flex: 0 0 auto;
	  flex: 0 0 auto;
	  display: block;
	  width: 230px;
	}
  }
  
  .app-sidebar__toggle {
	padding: 0 15px;
	color: $white;
	line-height: 2.4;
	-webkit-transition: background-color 0.3s ease;
	-o-transition: background-color 0.3s ease;
	transition: background-color 0.3s ease;
	z-index: 1;
  
	&:hover {
	  color: rgba(255, 255, 255, 0.5);
	}
  
	&:before {
	  content: "\f0c9";
	  transition: all .25s;
	  font-size: 21px;
	  font-family: 'Font Awesome 5 Free';
	  font-weight: 900;
	}
  
	&:focus, &:hover {
	  text-decoration: none;
	}
  }
  
  @media (max-width: 360px) {
	.app-sidebar__toggle {
	  line-height: 2;
	}
  }
  
  @media (max-width: 767px) {
	.app-sidebar__toggle {
	  -webkit-box-ordinal-group: 0;
	  -ms-flex-order: -1;
	  order: -1;
	}
  }
  
  .app-nav {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	margin-bottom: 0;
	-webkit-box-pack: end;
	-ms-flex-pack: end;
	justify-content: flex-end;
  }
  
  @media (min-width: 768px) {
	.app-nav {
	  -webkit-box-flex: 1;
	  -ms-flex: 1 0 auto;
	  flex: 1 0 auto;
	}
  }
  
  .app-nav__item {
	display: block;
	padding: 15px;
	line-height: 20px;
	color: #a8a8a8;
	-webkit-transition: background-color 0.3s ease;
	-o-transition: background-color 0.3s ease;
	transition: background-color 0.3s ease;
  
	&:hover, &:focus {
	  background: rgba(0, 0, 0, 0.1);
	  color: #f6f6f6;
	}
  }
  
  .app-search {
	position: relative;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-item-align: center;
	align-self: center;
	margin-right: 15px;
	padding: 10px 0;
  }
  
  @media (max-width: 480px) {
	.app-search {
	  display: none;
	}
  }
  
  .app-search__input {
	border: 0;
	padding: 5px 10px;
	padding-right: 30px;
	border-radius: 2px;
	background-color: rgba(255, 255, 255, 0.8);
	-webkit-transition: background-color 0.3s ease;
	-o-transition: background-color 0.3s ease;
	transition: background-color 0.3s ease;
  
	&::-webkit-input-placeholder, &:-ms-input-placeholder, &::-ms-input-placeholder, &::placeholder {
	  color: rgba(0, 0, 0, 0.4);
	}
  }
  
  .app-search__button {
	position: absolute;
	right: 0;
	top: 10px;
	bottom: 10px;
	padding: 0 10px;
	border: 0;
	color: rgba(0, 0, 0, 0.8);
	background: none;
	cursor: pointer;
  }
  
  .app-notification {
	min-width: 270px;
  }
  
  .app-notification__title {
	padding: 8px 20px;
	text-align: center;
	background-color: rgba(0, 150, 136, 0.4);
	color: #333;
  }
  
  .app-notification__footer {
	padding: 8px 20px;
	text-align: center;
	background-color: #eee;
  }
  
  .app-notification__content {
	max-height: 220px;
	overflow-y: auto;
  
	&::-webkit-scrollbar {
	  width: 6px;
	}
  
	&::-webkit-scrollbar-thumb {
	  background: rgba(0, 0, 0, 0.2);
	}
  }
  
  .app-notification__item {
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	padding: 8px 20px;
	color: inherit;
	-webkit-transition: background-color 0.3s ease;
	-o-transition: background-color 0.3s ease;
	transition: background-color 0.3s ease;
  
	&:focus, &:hover {
	  color: inherit;
	  text-decoration: none;
	  background-color: #e0e0e0;
	}
  }
  
  .app-notification__message, .app-notification__meta {
	margin-bottom: 0;
  }
  
  .app-notification__icon {
	padding-right: 10px;
  }
  
  .app-notification__message {
	line-height: 1.2;
  }
  
  .app-sidebar {
	position: fixed;
	top: 62px;
	bottom: 0;
	left: 0;
	z-index: 1000;
	height: 100%;
  
	/*background: transparent !important;
	-webkit-box-shadow: 0 2px 17px 2px rgb(235, 240, 255,0.2);
	box-shadow: 0 2px 17px 2px rgb(235, 240, 255,0.2);*/
