	.panel-tabs li {
	  a {
		padding: 15px 19.2px 15px 19.2px;
		color: #1c1d22;
		display: block;
		font-size: 11px;
		text-align: center;
  
		i {
		  display: block;
		  text-align: center;
		  font-size: 16px !important;
		  margin-bottom: 0.1rem;
		}
  
		&.active {
		  border: none;
		  position: relative;
  
		  &:before {
			content: "";
			position: absolute;
			top: auto;
			bottom: 0px;
			left: 0;
			right: 0;
			width: auto;
			height: 2px;
			border-radius: 0px 50px 50px 0px;
		  }
		}
  
		&:hover:before {
		  content: "";
		  position: absolute;
		  top: auto;
		  bottom: 0px;
		  left: 0;
		  right: 0;
		  width: auto;
		  height: 2px;
		  border-radius: 0px 50px 50px 0px;
		}
	  }
  
	  &:hover {
		border-right: 1px solid $border !important;
	  }
  
	  a:hover {
		position: relative;
	  }
	}
  }
  
